"use client"
import { getComicsByCharacterId } from "@/lib/api"
import { useEffect, useState } from "react"

const ComicDetails = ({ characterId }: { characterId: string }) => {
  const [comics, setComics] = useState(null)

  useEffect(() => {
    const fetchData = async () => {
      const data = await getComicsByCharacterId(characterId)
      setComics(data)
    }
    fetchData()
  }, [characterId])
 
  return <div className="text-7xl text-red-200">{characterId}</div>
}

export default ComicDetails
